import {
  Component,
  Host,
  h,
  Prop,
  State,
  Listen,
  Event,
  EventEmitter,
  Method,
} from '@stencil/core';
import { parseIfString } from '@springbokagency/stencil-base';
import { BaseModalId } from '../../organisms/base-modal/base-modal.types';
import { ColorTheme, TouchDirections } from './default-layout.types';
import { BsImageCarousel } from '../../organisms/image-carousel/image-carousel';
import { BsExperienceZoneModal } from '../../molecules/experience-zone-modal/experience-zone-modal';
import { BsTestBadgeModal } from '../../organisms/test-badge-modal/test-badge-modal';
import { HeaderNavigation } from '../../organisms/overlay-menu/overlay-menu.types';

/**
 * Default layout component.
 */
@Component({
  tag: 'bs-default-layout',
  styleUrl: 'default-layout.scss',
  shadow: true,
})
export class BsDefaultLayout {
  private touchStartX = 0;

  private touchEndX = 0;

  private touchThreshold = 150;

  /** States */
  @State() overlayMenuIsOpen: boolean;

  @State() overlayMenuData: HeaderNavigation;

  @State() youtubeModalIsOpen: boolean;

  @State() youtubeModalData: { videoId: string; mute: boolean };

  @State() carouselModalIsOpen: boolean;

  @State() carouselModalData: BsImageCarousel;

  @State() experienceZoneModalIsOpen: boolean;

  @State() experienceZoneModalData: BsExperienceZoneModal;

  @State() testBadgeModalIsOpen: boolean;

  @State() testBadgeModalData: BsTestBadgeModal;

  /** Props */
  /**
   * Color theme
   */
  @Prop() theme: ColorTheme = ColorTheme.Default;

  /** Listeners */

  @Listen('bsClickAction')
  @Listen('bsButtonClick')
  bsClickActionHandler(e: CustomEvent) {
    e.stopPropagation();

    const name = e.detail.name || e.detail.action;

    switch (name) {
      case 'open-overlay-menu':
        this.overlayMenuData = e.detail.value;
        this.overlayMenuIsOpen = true;
        this.disableScroll();
        break;

      case 'open-youtube-modal':
        this.youtubeModalData = parseIfString(e.detail.value);
        this.youtubeModalIsOpen = true;
        this.disableScroll();
        break;

      case 'open-carousel-modal':
        this.carouselModalData = e.detail.value;
        this.carouselModalIsOpen = true;
        this.disableScroll();
        break;

      case 'open-experience-zone-modal':
        this.experienceZoneModalData = e.detail.value;
        this.experienceZoneModalIsOpen = true;
        this.disableScroll();
        break;

      case 'open-test-badge-modal':
        this.testBadgeModalData = parseIfString(e.detail.value);
        this.testBadgeModalIsOpen = true;
        this.disableScroll();
        break;

      default:
        break;
    }
  }

  @Listen('bsModalIsClosed')
  bsModalIsClosedHandler(e: CustomEvent) {
    this.enableScroll();

    switch (e.detail.modalId) {
      case BaseModalId.OverlayMenu:
        this.overlayMenuIsOpen = false;
        break;

      case BaseModalId.Media:
        this.youtubeModalIsOpen = false;
        this.carouselModalIsOpen = false;
        this.experienceZoneModalIsOpen = false;
        this.testBadgeModalIsOpen = false;
        break;

      default:
        break;
    }
  }

  @Listen('touchstart')
  handleTouchStart(e: TouchEvent) {
    this.touchStartX = e.touches[0].clientX;
  }

  @Listen('touchend')
  handleTouchEnd(e: TouchEvent) {
    this.touchEndX = e.changedTouches[0].clientX;
    this.handleTouch();
  }

  /** Events */
  /**
   * Emitted when a swipe action is detected.
   */
  @Event() bsSwipeAction: EventEmitter<{ direction: TouchDirections }>;

  /**
   * Emitted when the youtube API script triggers the window.onYouTubeIframeAPIReady method.
   */
  @Event() bsYoutubeAPIReady: EventEmitter;

  /** Methods */
  @Method()
  async handleTouch() {
    if (this.touchEndX - this.touchStartX < -this.touchThreshold) {
      this.bsSwipeAction.emit({ direction: TouchDirections.Left });
    }

    if (this.touchEndX - this.touchStartX > this.touchThreshold) {
      this.bsSwipeAction.emit({ direction: TouchDirections.Right });
    }
  }

  // eslint-disable-next-line class-methods-use-this
  private disableScroll = () => {
    document.body.style.height = '100vh';
    document.body.style.overflow = 'hidden';
  };

  // eslint-disable-next-line class-methods-use-this
  private enableScroll = () => {
    document.body.style.height = '';
    document.body.style.overflow = '';
  };

  /** lifecycle hooks */
  componentDidLoad() {
    window.onYouTubePlayerAPIReady = () => {
      this.bsYoutubeAPIReady.emit();
    };
  }

  render() {
    return (
      <Host>
        <div class={`default-layout`}>
          {/* Overlay Menu */}
          {this.overlayMenuIsOpen && (
            <bs-overlay-menu header-navigation={this.overlayMenuData}></bs-overlay-menu>
          )}

          <main>
            <slot />
          </main>

          {/* Youtube modal */}
          {this.youtubeModalIsOpen && <bs-youtube-video {...this.youtubeModalData} />}

          {/* Carousel modal */}
          {this.carouselModalIsOpen && <bs-image-carousel {...this.carouselModalData} />}

          {/* Experience Zone modal */}
          {this.experienceZoneModalIsOpen && (
            <bs-experience-zone-modal {...this.experienceZoneModalData} />
          )}

          {/* Test Badge modal */}
          {this.testBadgeModalIsOpen && <bs-test-badge-modal {...this.testBadgeModalData} />}
        </div>
      </Host>
    );
  }
}
